<template>
  <div>
    <el-table stripe style="width: 100%" :data="tableData" border>
      <el-table-column label="选择网关节点" >
        <el-table-column prop="IP" label="IP地址"></el-table-column>
        <el-table-column prop="port" label="端口"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="handlecopy(scope.$index, scope.row)">复制</el-button>
          <el-button size="mini" @click="handledelete(scope.$index, scope.row)">删除</el-button>
          <el-button size="mini" @click="handleselect()">选择服务</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="服务选择" :visible.sync="showtable" center>
      <el-form :model="form">
        <el-form-item label="负载均衡选择：">
          <el-radio-group v-model="form.select" size="medium">
            <el-radio-button label="1">负载均衡1</el-radio-button>
            <el-radio-button label="2">负载均衡2</el-radio-button>
            <el-radio-button label="3">负载均衡3</el-radio-button>
            <el-radio-button label="4">负载均衡4</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="dialogCancel()">取消</el-button>
        <el-button type="primary" @click="dialogConfirm()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "page1",
  data(){
    return{
      tableData: [],
      form: {
        select: "1"
      },
      showtable: false
    }
  },

  created() {
    this.$http.get("/mock/admits").then(res=>{
      console.log(res.data)
      this.tableData = res.data
    })

  },
  methods: {
    handledelete(index, row) {
      self = this
      setTimeout(()=>{
        console.log(index, row)
        self.tableData.splice(index, 1)
        self.open(1)
      }, 500)
    },
    handlecopy(index, row){
      self = this
      setTimeout(()=>{
        console.log(index, row)
        self.tableData.splice(index+1, 0, row)
        self.open(2)
      }, 1000)
    },
    handleselect(){
      this.showtable = true
    },
    dialogCancel(){
      this.showtable = false
    },
    dialogConfirm(){
      this.showtable = false
    },
    open(flag){
      if (flag==1){
        this.$message({
          message: '删除成功',
          type: 'error'
        })
      }
      else if(flag==2){
        this.$message({
          message: '添加成功',
          type: 'success'
        })
      }
    }
  }

}
</script>

<style scoped>

</style>



