<template>
  <div class="body">
    <el-tabs type="card" v-model="activate_tab">
      <el-tab-pane label="1+1模式" name="1" class="card">
        <el-row :gutter="20" v-for="(card, index) in result_1" :key="index">
          <el-col :span="6" v-for="(card_sub, index_sub) in card" :key="index_sub">
            <el-card class="box-card" shadow="hover">
              <div slot="header">
                <span>卡片名称</span>
                <el-button type="text" @click="handlecancel(index, index_sub, 1)">删除</el-button>
                <el-button type="text" @click="handlecopy(index, index_sub, 1)">复制</el-button>
              </div>
              <div>
                <p><span class="content_title">IP地址：</span><span class="content_detail">{{card_sub.IP}}</span></p>
                <p><span class="content_title">端口地址: </span><span class="content_detail">{{card_sub.port}}</span></p>
                <p><span class="content_title">状态：</span><span class="content_detail">{{card_sub.status}}</span></p>
                <p><span class="content_title">延迟：</span><span class="content_detail">{{card_sub.delay}}ms</span></p>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="1*1模式" name="2" class="card">
        <el-row :gutter="20" v-for="(card, index) in result_2" :key="index">
          <el-col :span="6" v-for="(card_sub, index_sub) in card" :key="index_sub">
            <el-card class="box-card" shadow="hover">
              <div slot="header">
                <span>卡片名称</span>
                <el-button type="text" @click="handlecancel(index, index_sub, 2)">删除</el-button>
                <el-button type="text" @click="handlecopy(index, index_sub, 2)">复制</el-button>
              </div>
              <div>
                <p><span class="content_title">IP地址：</span><span class="content_detail">{{card_sub.IP}}</span></p>
                <p><span class="content_title">端口地址: </span><span class="content_detail">{{card_sub.port}}</span></p>
                <p><span class="content_title">状态：</span><span class="content_detail">{{card_sub.status}}</span></p>
                <p><span class="content_title">延迟：</span><span class="content_detail">{{card_sub.delay}}ms</span></p>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "page2",
  data(){
    return{
      activate_tab: "1",
      cardData_1: [],
      cardData_2: [],
      result_1: [],
      result_2: []
    }
  },
  created(){
    this.initData(1)
    this.initData(2)
  },
  methods:{
    initData(flag){
      if(flag==1){
        this.$http.get('/mock/plus').then(res=>{
          this.cardData_1 = res.data
          console.log(this.chunk(this.cardData_1, 4))
          this.result_1 = this.chunk(this.cardData_1, 4)
        })
      }
      else if(flag==2){
        this.$http.get('/mock/multi').then(res=>{
          this.cardData_2 = res.data
          console.log(this.chunk(this.cardData_2, 4))
          this.result_2 = this.chunk(this.cardData_2, 4)
        })
      }
    },
    chunk(arrays, size){
      const length = arrays.length
      if (!length || !size || size < 1) {
        return []
      }
      let index =0
      let resIndex = 0
      let result = new Array(Math.ceil(length/size))
      while(index<length){
        result[resIndex++] = arrays.slice(index, index+=size)
      }
      return result
    },

    handlecancel(index, index_sub, flag){
      let self = this
      setTimeout(()=>{
        if(flag==1){
          self.cardData_1.splice(index*4+index_sub, 1)
          self.result_1 = this.chunk(this.cardData_1, 4)
          self.open(1)
        }
        else if(flag==2){
          self.cardData_2.splice(index*4+index_sub, 1)
          self.result_2 = this.chunk(this.cardData_2, 4)
          self.open(1)
        }
      }, 500)
    },

    handlecopy(index, index_sub, flag) {
      let self = this
      setTimeout(()=>{
        if(flag==1) {
          self.cardData_1.splice(index*4+index_sub+1, 0, this.cardData_1[index*4+index_sub])
          self.result_1 = this.chunk(this.cardData_1, 4)
          self.open(2)
        }
        else{
          self.cardData_2.splice(index*4+index_sub+1, 0, this.cardData_2[index*4+index_sub])
          self.result_2 = this.chunk(this.cardData_2, 4)
          self.open(2)
        }
      }, 500)
    },
    open(flag){
      if (flag==1){
        this.$message({
          message: '删除成功',
          type: 'error'
        })
      }
      else if(flag==2){
        this.$message({
          message: '添加成功',
          type: 'success'
        })
      }
    }
  }
}
</script>

<style scoped>
.body {
  height: 500px;
}

.el-row {
  margin-bottom: 20px;
}

.box-card {
  width: 100%;
}

.box-card .el-button{
  float: right;
  margin-right: 20px;
}

.body .card{

}

.body .card .content_title{
  font-weight: bold;
}

.body .card .content_detail{
  float: right;
  margin-right: 15px;
}

</style>



